import { _get, _post, _delete } from './index'

// 
export const login = (data: any) => {
  const req = {
    data,
    url: '/api/login'
  }
  return _post(req)
}

// 
export const searchUserList = (data: any) => {
  const req = {
    data,
    url: '/api/searchuserlist'
  }
  return _get(req)
}

// 
export const saveSysUser = (data: any) => {
  const req = {
    data,
    url: '/api/savesysuser'
  }
  return _post(req)
}

// 
export const getColumns = (data: any) => {
  const req = {
    data,
    url: '/api/getcolumns'
  }
  return _get(req)
}
